.rrze-events,
.rrze-speaker,
.rrze-talk {
    .sr-only {
        clip: rect(0, 0, 0, 0);
        border: 0;
        height: 1px;
        min-height: 1px;
        min-width: 1px;
        overflow: hidden;
        padding: 0;
        position: absolute;
        white-space: nowrap;
        width: 1px;
    }
    a {
        &:hover, &:focus, &:active {
            svg.rrze-elements-icon {
                path {
                    fill: var(--color-button-text, #ffffff);
                }
            }
        }
    }
}

.rrze-2019 {
    &.single-speaker,
    &.single-talk {
        #primary {
            @media screen and (min-width: 1600px) {
                padding-left: 0;
                margin: 0 auto;
            }
        }
    }
}
